<template>
  <div class="paper-page" v-if="paper">
    <h2>{{ paper.title }}</h2>
    <p>{{ paper.description }}</p>
    <br />

    <el-form :model="form">
      <div v-for="(question, index) in paper.questions" :key="question.id" class="question-card">
        <p class="question-title">Q{{ index + 1 }}: {{ question.content }}</p>
        <el-radio-group v-model="form['q' + question.orderIndex]">
          <el-radio v-for="option in question.options" :key="option.value" :label="option.value" style="display: block">
            {{ option.value }}. {{ option.label }}
          </el-radio>
        </el-radio-group>
      </div>

      <el-form-item>
        <el-button type="primary" @click="handleSubmit">Submit</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "PaperFillPage",
  data() {
    return {
      asresultId: null,
      paper: null,
      form: {},
    };
  },

  mounted() {
    const asresultId = Number(this.$route.params.asresultId);
    if (!asresultId) {
      this.$message.error("缺少答卷ID，无法加载试卷！");
      this.$router.push("/index/paper/list");
      return;
    }

    console.log("进入答题页面，asresultId =", asresultId);
    this.asresultId = asresultId;

    // 统一请求 fill 接口
    this.loadPaper();
  },

  methods: {
    // 提交答卷
    handleSubmit() {
      const submitData = {
        asresultId: this.asresultId,
        userId: localStorage.getItem("userId"), // 👈 从本地存储取
        paperId: this.paper ? this.paper.paperId : null, // 👈 从试卷对象取
        answers: this.form,
      };

      console.log("提交数据：", submitData);

      this.$axios
        .post(this.$httpUrl + "/asresult/submitWithScore", submitData)
        .then((response) => {
          if (response.data.code === 1) {
            this.$message.success("Submit Successful!");
            this.$router.push(`/index/paper/result/${this.asresultId}`);
          } else {
            this.$message.error("Submit failed：" + response.data.msg);
          }
        })
        .catch((error) => {
          this.$message.error("提交异常");
          console.error("提交错误：", error);
        });
    },

    // 加载试卷（整卷 or 错题卷）
    loadPaper() {
      this.$axios
        .get(this.$httpUrl + "/paper/fill", {
          params: { asresultId: this.asresultId },
        })
        .then((res) => {
          const paper = res.data.data || { questions: [] };
          this.paper = paper;
          this.form = {};

          // 初始化答题表单（全部置空）
          paper.questions.forEach((q) => {
            const key = "q" + q.orderIndex;
            this.$set(this.form, key, "");
          });
        })
        .catch((err) => {
          console.error("加载试卷失败：", err);
          this.$message.error("加载试卷失败！");
        });
    },
  },
};
</script>

<style scoped>
.paper-page {
  padding: 20px;
  max-width: 700px;
  margin: 0 auto;
}

.question-card {
  max-width: 680px;
  margin: 0 auto 24px auto;
  padding: 20px;
  border: 1px solid #e4e7ed;
  border-radius: 8px;
  background-color: #fff;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.03);
}

.question-card:hover {
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.08);
}

.question-title {
  font-weight: 600;
  font-size: 16px;
  margin-bottom: 12px;
}
</style>
