<template>
  <div style="display: flex; align-items: center; line-height: 60px">
    <div>
      <i :class="icon" style="font-size: large" @click="collapseAside"></i>
    </div>

    <div
      style="
        flex: 1;
        text-align: center;
        font-size: xx-large;
        font-weight: bold;
      "
    >
      <span>Vesuvius Admin System</span>
    </div>

    <div style="font-size: small">
      <span style="cursor: pointer">
        {{ username }}
      </span>

      <el-dropdown trigger="click">
        <i class="el-icon-caret-bottom" style="margin: 5px"></i>
        <el-dropdown-menu slot="dropdown">
          <!-- <el-dropdown-item @click.native="toUser">profile</el-dropdown-item> -->
          <el-dropdown-item @click.native="logout">log out</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>

    <!-- 登录弹窗 -->
    <el-dialog title="Login" :visible.sync="loginDialogVisible" width="30%">
      <!--标题显示靠右，手动调整为靠左 -->
      <template #title>
        <span
          style="
            font-size: 18px;
            font-weight: bold;
            display: inline-block;
            width: 100%;
            text-align: left;
          "
        >
          Login
        </span>
      </template>
      <el-form
        :model="loginForm"
        :rules="loginRules"
        ref="loginForm"
        label-width="100px"
        style="width: 80%; margin-left: 30px"
      >
        <el-form-item label="Account" prop="account">
          <el-input v-model="loginForm.account" autocomplete="off" />
        </el-form-item>
        <el-form-item label="Password" prop="password">
          <el-input
            v-model="loginForm.password"
            type="password"
            autocomplete="off"
          />
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="loginDialogVisible = false">Cancel</el-button>
        <el-button type="primary" @click="submitLogin">Login</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "HeaderBar",
  data() {
    return {
      // 数据
      loginDialogVisible: false, // 登录弹窗是否可见
      username: localStorage.getItem("username") || "User", // 用户名
      loginForm: {
        account: "",
        password: "",
      },
      loginRules: {
        account: [
          { required: true, message: "Please enter account", trigger: "blur" },
        ],
        password: [
          { required: true, message: "Please enter password", trigger: "blur" },
        ],
      },
    };
  },
  props: {
    // 属性
    icon: String, // 图标
  },
  methods: {
    // 方法
    toUser() {
      console.log("toUser");
    },
    logout() {
      // 清除本地存储的登录信息
      localStorage.removeItem("token");
      localStorage.removeItem("userId");
      // 可选：显示提示
      this.$message.warning("Logged out successfully");
      // 调用后端 /exit 关闭 Flask 服务
      this.$axios
        .get(this.$httpUrl + "/exit")
        .then(() => {
          setTimeout(() => {
            // 刷新或关闭页面
            window.close(); // 关闭浏览器标签
          }, 200);
        })
        .catch((err) => {
          console.error("Fail to log out", err);
          // 即使 Flask 没有关成功，前端也照常刷新
          setTimeout(() => {
            location.reload();
          }, 200);
        });
    },

    collapseAside() {
      this.$emit("doCollapseAside");
    },

    submitLogin() {
      this.$refs.loginForm.validate((valid) => {
        if (valid) {
          this.$axios
            .post(this.$httpUrl + "/user/login", this.loginForm)
            .then((res) => {
              if (res.data.code === 1) {
                this.$message.success("Login successful");
                this.loginDialogVisible = false;
                // 可选：保存用户信息
                localStorage.setItem("user", JSON.stringify(res.data.data));
              } else {
                this.$message.error(res.data.msg || "Login failed");
              }
            })
            .catch(() => {
              this.$message.error("Server error");
            });
        }
      });
    },
  },
};
</script>

<style scoped>
/* 样式 */
</style>
