<template>
  <div>
    <!-- 水平分布标题+Export Excel按钮 -->
    <div style="display: flex; justify-content: space-between; align-items: center; font-size: 18px; font-weight: bold; margin: 15px 10px">
      <div>PO Records - Batch ID: {{ batchId }}</div>
      <el-button type="primary" plain size="mini" icon="el-icon-download" @click="exportExcel"> Export Excel </el-button>
    </div>

    <el-table
      :data="poRecords"
      :header-cell-style="{
        background: '#dfe6ee',
        color: '#333',
        fontWeight: 'bold',
        textAlign: 'center',
      }"
      :cell-style="{ textAlign: 'center' }"
      border
      style="width: 100%"
    >
      <el-table-column label="No." type="index" width="60" />
      <el-table-column prop="poNumber" label="PO Number" width="140" />
      <el-table-column prop="productName" label="Product Name" />
      <el-table-column prop="container" label="Container ID" width="160" />
      <el-table-column prop="vessel" label="Vessel" />
      <el-table-column prop="weight" label="Weight" width="120" />
      <el-table-column prop="etd" label="ETD" width="120" />
      <el-table-column prop="eta" label="ETA" width="120" />
      <el-table-column prop="sourceFile" label="File Name" />
    </el-table>
  </div>
</template>

<script>
// 导出按钮事件方法
import * as XLSX from "xlsx";
import { saveAs } from "file-saver";
export default {
  name: "PoDetailPage",
  data() {
    return {
      batchId: this.$route.params.batchId,
      poRecords: [],
    };
  },
  mounted() {
    this.fetchPoRecords();
  },
  methods: {
    async fetchPoRecords() {
      const res = await this.$axios.get(this.$httpUrl + `/po/records/${this.batchId}`);
      this.poRecords = res.data.data;
    },

    exportExcel() {
      // 准备导出的数据
      const exportData = this.poRecords.map((item, index) => ({
        "No.": index + 1,
        "PO Number": item.poNumber,
        "Product Name": item.productName,
        "Container ID": item.container,
        Vessel: item.vessel,
        Weight: item.weight,
        ETD: item.etd,
        ETA: item.eta,
        "File Name": item.sourceFile,
      }));

      // 创建 worksheet 和 workbook
      const worksheet = XLSX.utils.json_to_sheet(exportData);
      const workbook = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(workbook, worksheet, "PO Records");

      // 导出成 Blob 对象并下载
      const wbout = XLSX.write(workbook, { bookType: "xlsx", type: "array" });
      const blob = new Blob([wbout], { type: "application/octet-stream" });

      saveAs(blob, `PO_Records_Batch_${this.batchId}.xlsx`);
    },
  },
};
</script>
