<template>
  <div class="component-name">
    <el-menu
      style="border: none"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b"
      :default-active="$route.path"
      router
      class="el-menu-vertical-demo"
      :collapse="isCollapse"
      :collapse-transition="false"
    >
      <!-- 首页使用静态 index 自动跳转 -->
      <el-menu-item :index="'/index/home'">
        <i class="el-icon-s-home"></i>
        <span>Home</span>
      </el-menu-item>

      <!-- 用户列表：管理员专属菜单 -->
      <el-menu-item :index="'/index/userlist'" v-if="isAdmin">
        <i class="el-icon-user-solid"></i>
        <span>User List</span>
      </el-menu-item>

      <!-- Assessment 菜单 -->
      <el-menu-item index="/index/paper/listAsresult">
        <i class="el-icon-s-order"></i>
        <span>Assessment</span>
      </el-menu-item>

      <!-- PoRecords 菜单 -->
      <!-- <el-menu-item index="/index/po/porecords">
        <i class="el-icon-s-order"></i>
        <span>Purchase Orders</span>
      </el-menu-item> -->
    </el-menu>
  </div>
</template>

<script>
export default {
  name: "AsideBar",
  props: {
    isCollapse: Boolean,
  },
  computed: {
    userId() {
      return localStorage.getItem("userId");
    },
    roleId() {
      return Number(localStorage.getItem("roleId"));
    },
    isAdmin() {
      return this.roleId == 0;
    },
  },
  methods: {
    // 动态路由跳转，防止重复导航报错
    safePush(path) {
      if (this.$route.path !== path) {
        this.$router.push(path);
      }
    },
  },
};
</script>

