import Vue from "vue";
import VueRouter from "vue-router";
import LayoutPage from "@/components/Layout.vue"; // 外层框架页
import UserListPage from "@/components/pages/UserList.vue"; // 用户管理页
import LoginPage from "@/components/pages/Login.vue";
import PaperListPage from "@/components/pages/paper/PaperList.vue"; // 问卷列表页
import PaperFillPage from "@/components/pages/paper/PaperFill.vue"; // 问卷答题页
import PaperResultPage from "@/components/pages/paper/PaperResult.vue"; // 问卷结果页
import HomePage from "@/components/pages/Home.vue"; // 首页（可选）
import PoRecords from "@/components/pages/po/PoList.vue"; // 购物订单页
import PoDetailPage from "@/components/pages/po/PoDetail.vue"; // 购物订单详情页

Vue.use(VueRouter);

// 创建并导出唯一 router 实例
const router = new VueRouter({
  mode: "history",
  routes: [
    {
      path: "/",
      redirect: "/login",
    },
    {
      path: "/login",
      component: LoginPage,
    },
    {
      path: "/index",
      component: LayoutPage,
      children: [
        {
          path: "",
          redirect: "home", // 默认重定向到首页
        },
        {
          path: "home",
          name: "HomePage",
          component: HomePage, // 首页（可选）
        },
        {
          path: "userlist",
          name: "UserListPage",
          component: UserListPage,
        },
        {
          path: "paper/listAsresult",
          component: PaperListPage,
          name: "PaperListPage",
        },
        // 两个路径指向的是同一个组件文件
        {
          path: "paper/fill/:asresultId", //考卷页路径，需要获取元信息MetaInfo
          component: PaperFillPage,
          name: "PaperFillPage", // 👈 考卷页
        },
        {
          path: "paper/result/:asresultId",
          component: PaperResultPage,
          name: "PaperResultPage",
        },
        {
          path: "po/porecords",
          component: PoRecords,
          name: "PoRecordsPage",
        },
        {
          path: "/po/detail/:batchId",
          component: PoDetailPage,
          name: "PoDetailPage",
        },
      ],
    },
    {
      path: "*",
      redirect: "/index/home", // 其他未知路径，自动重定向到首页
    },
  ],
});

// 加登录守卫 + 管理员权限控制
router.beforeEach((to, from, next) => {
  const token = localStorage.getItem("token");
  const userId = localStorage.getItem("userId");
  const roleId = Number(localStorage.getItem("roleId"));

  // 未登录 → 强制跳转登录页
  if (!token && to.path !== "/login") {
    return next("/login");
  }

  // 非管理员访问 userlist → 跳回首页
  if (to.path === "/index/userlist" && roleId !== 0) {
    return next("/index/paper/list/" + userId);
  }

  next(); // 正常放行
});

export default router;
