<template>
  <div class="home-container">
    <el-card shadow="hover" class="home-card">
      <!-- 添加 logo 图标 -->
      <img :src="require('@/assets/images/logo-corporate.svg')" alt="Vesuvius Logo" class="logo-image" />

      <div class="title">Welcome to the Vesuvius Admin System</div>
      <div class="subtitle">We're shaping the future of industries that will shape the future for everyone.</div>

      <el-button type="primary" @click="loginDialogVisible = true" style="margin-top: 20px"> Login </el-button>
    </el-card>

    <!-- 登录弹窗 -->
    <el-dialog title="Login" :visible.sync="loginDialogVisible" width="500px">
      <el-form :model="loginForm" label-width="100px">
        <el-form-item label="Account" style="margin-left: 8%">
          <el-input v-model="loginForm.account" style="width: 240px" @keydown.native.enter="submitLogin" />
        </el-form-item>
        <el-form-item label="Password" style="margin-left: 8%">
          <el-input v-model="loginForm.password" type="password" style="width: 240px" @keydown.native.enter="submitLogin" />
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="loginDialogVisible = false">Cancel</el-button>
        <el-button type="primary" @click="submitLogin">Login</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "LoginPage",
  data() {
    return {
      loginDialogVisible: false,
      loginForm: {
        account: "",
        password: "",
      },
    };
  },
  methods: {
    submitLogin() {
      this.$axios
        .post(this.$httpUrl + "/user/login", {
          account: this.loginForm.account,
          password: this.loginForm.password,
        })
        .then((res) => {
          if (res.data.code === 1) {
            const token = res.data.data.token;
            const user = res.data.data.user;
            
            // 保存用户信息到 localStorage
            const userId = user.userId;
            const username = user.name;
            const identityId = user.identityId;
            const roleId = user.roleId;
            const account = user.account;

            localStorage.setItem("token", token);
            localStorage.setItem("userId", userId);
            localStorage.setItem("username", username);
            localStorage.setItem("roleId", roleId);
            localStorage.setItem("account", account);
            localStorage.setItem("identityId", identityId);

            this.$message.success("Login successful");
            console.log("Login successful", res.data);

            // 跳转首页
            this.$router.push("/index");
          } else {
            this.$message.error(res.data.msg || "Login failed");
          }
        });
    },
  },
};
</script>

<style scoped>
.logo-image {
  width: 140px; /* 控制宽度，常用 */
  height: auto; /* 保持纵横比 */
  margin-bottom: 20px;
}

.home-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  background: linear-gradient(to right, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.05)), url("/images/pipes-homebg.jpg") no-repeat center center;
  background-size: cover; /* 让图片铺满整个容器 */
}

.home-card {
  width: 800px;
  text-align: center;
  padding: 40px 20px;
  margin-top: -200px;
  position: relative;
  z-index: 1;
}

.title {
  font-size: 24px;
  font-weight: bold;
  color: #333;
}

.subtitle {
  margin-top: 10px;
  font-size: 14px;
  color: #999;
}
</style>
