<template>
  <div>
    <!-- 水平分布标题+upload按钮 -->
    <div style="display: flex; justify-content: space-between; align-items: center; font-size: 18px; font-weight: bold; margin: 15px 10px">
      <div>Upload Records</div>
      <el-button type="primary" plain size="mini" icon="el-icon-upload" @click="dialogVisible = true"> Upload Files </el-button>
    </div>

    <!-- 小窗口：弹窗上传 -->
    <el-dialog title="Upload Files" :visible.sync="dialogVisible" width="500px" :close-on-click-modal="false">
      <div style="padding: 0 20px">
        <el-upload
          ref="uploadRef"
          drag
          multiple
          :file-list="fileList"
          :before-upload="beforeUpload"
          :auto-upload="false"
          :on-change="handleFileChange"
        >
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">Drag files to here, or <em>click here to upload files</em></div>
          <div class="el-upload__tip" slot="tip">Only PDF supported</div>
        </el-upload>
      </div>

      <span slot="footer" style="display: flex; justify-content: flex-end; padding: 0 20px">
        <el-button @click="dialogVisible = false">Cancel</el-button>
        <el-button type="primary" @click="submitUpload">Submit</el-button>
      </span>
    </el-dialog>

    <!-- list清单列表 -->
    <el-table
      :data="batchList"
      :header-cell-style="{
        background: '#dfe6ee',
        color: '#333',
        fontWeight: 'bold',
        textAlign: 'center',
      }"
      :cell-style="{ textAlign: 'center' }"
      border
      style="width: 100%"
    >
      <el-table-column label="No." type="index" width="60" />

      <el-table-column prop="batchId" label="Batch ID" />

      <el-table-column prop="createTime" label="Upload Time">
        <template v-slot="scope">
          {{ formatDate(scope.row.createTime) }}
        </template>
      </el-table-column>

      <el-table-column prop="fileCount" label="File Count" width="120" />

      <el-table-column label="Action" width="220px">
        <template slot-scope="scope">
          <el-button type="text" size="small" @click="viewDetails(scope.row.batchId)">View Details</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>

export default {
  name: "PoRecords",
  created() {
    // 跳转页面时自动执行
    this.loadBatchListFromServer();
  },

  data() {
    return {
      uploadUrl: this.$httpUrl + "/po/extractSave", // 后端接口地址
      currentUserId: Number(localStorage.getItem("userId")), // 可根据登录用户设置
      batchList: [], // ✅ 新增，用于展示上传记录表格

      // 弹窗上传字段
      dialogVisible: false,
      fileList: [],
    };
  },

  methods: {
    // 文件上传前的验证
    beforeUpload(file) {
      const isPDF = file.type === "application/pdf";
      if (!isPDF) this.$message.error("Only PDF files are allowed.");
      return isPDF;
    },
    // 处理文件选择变化
    handleFileChange(file, fileList) {
      this.fileList = fileList;
    },
    // 提交上传
    async submitUpload() {
      if (!this.fileList.length) {
        this.$message.warning("Please select files to upload.");
        return;
      }

      const formData = new FormData();
      this.fileList.forEach((file) => {
        formData.append("file", file.raw); // 你后端接收的字段名是 'file'
      });
      // ✅ 加上传 userId 字段（后端需要）
      formData.append("userId", this.currentUserId); // 假设你在 data() 里已经存好了 currentUserId

      try {
        await this.$axios.post(this.$httpUrl + "/po/extractSave", formData, {
          headers: { "Content-Type": "multipart/form-data" },
        });
        this.$message.success("Upload successful!");
        this.dialogVisible = false;
        this.fileList = [];

        // 可选：调用刷新列表的方法
        this.loadBatchListFromServer(); // ✅ 这是你页面顶部定义的刷新清单的方法
      } catch (err) {
        console.error("Upload failed", err);
        this.$message.error("Upload failed!");
      }
    },

    // 初始化加载页面list
    loadBatchListFromServer() {
      const userId = localStorage.getItem("userId"); // 从 localStorage 获取当前用户
      console.log("当前userID是" + userId);
      this.$axios
        .get(this.$httpUrl + `/po/listByUser?userId=${userId}`)
        .then((res) => {
          if (res.data.code === 1) {
            this.batchList = res.data.data;
          } else {
            this.$message.error(res.data.msg || "Failed to load upload records.");
          }
        })
        .catch(() => {
          this.$message.error("Server error.");
        });
    },

    viewDetails(row) {
      const batchId = row.batchId || row; // 处理传入的行数据或直接传入batchId
      this.$router.push(`/po/detail/${batchId}`);
    },

    formatDate(datetime) {
      if (!datetime) return "";
      return new Date(datetime).toLocaleString(); // 或自定义格式
    },
  },
};
</script>

<style scoped>
.el-descriptions {
  margin-bottom: 20px;
}
</style>
