<template>
  <div class="result-page" v-if="result">
    <!-- 概览 -->
    <el-card class="summary-card" shadow="hover">
      <div class="summary-item"><strong>PAPER:</strong> {{ result.title }}</div>
      <div class="summary-item"><strong>USER:</strong> {{ result.username }}</div>
      <div class="summary-item"><strong>SCORE:</strong> {{ result.score }}</div>
      <div class="summary-item">
        <strong>STATUS:</strong>
        <el-tag :type="result.passed ? 'success' : 'danger'">
          {{ result.passed ? "PASSED" : "FAILED" }}
        </el-tag>
      </div>
      <div class="summary-item"><strong>SUBMITTED:</strong> {{ formatDate(result.submitTime) }}</div>
    </el-card>

    <!-- 每题 -->
    <el-card v-for="(q, index) in result.questions" :key="index" class="question-card" shadow="never">
      <div class="question-header">
        <span class="q-key">Q{{ index + 1 }}</span>
        <!-- ✅ 显示 Q1/Q2 -->
        <span class="q-title">{{ q.questionTitle }}</span>
        <el-tag :type="q.isCorrect ? 'success' : 'danger'" class="q-tag">
          {{ q.isCorrect ? "Correct" : "Wrong" }}
        </el-tag>
      </div>

      <div class="question-section">
        <div><strong>Your Answer:</strong> {{ q.userAnswer.join(", ") || "-" }}</div>
        <!-- 暂时不显示正确答案 -->
        <!-- <div><strong>Correct Answer:</strong> {{ q.correctAnswer.join(", ") }}</div> -->
      </div>

      <div class="options">
        <strong>Options:</strong>
        <ul>
          <li v-for="(opt, idx) in q.options" :key="idx">{{ String.fromCharCode(65 + idx) }}. {{ opt }}</li>
        </ul>
      </div>
    </el-card>

    <!-- 页面最下方添加 -->
    <div style="text-align: center; margin-top: 40px">
      <el-button type="primary" icon="el-icon-arrow-left" @click="goBack"> Back to List </el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "PaperResultPage",
  data() {
    return {
      result: null,
    };
  },
  methods: {
    formatDate(val) {
      if (!val) return "-";
      const date = new Date(val);
      return date
        .toLocaleString("en-AU", {
          timeZone: "Australia/Sydney",
          day: "2-digit",
          month: "2-digit",
          year: "numeric",
          hour: "2-digit",
          minute: "2-digit",
          hour12: true,
        })
        .replace(",", "");
    },

    goBack() {
      const userId = localStorage.getItem("userId");
      if (!userId) {
        this.$message.error("无法返回列表：userId 不存在");
        return;
      }
      this.$router.push(`/index/paper/listAsresult`);
    },
  },

  mounted() {
    const asresultId = this.$route.params.asresultId;

    this.$axios
      .post(this.$httpUrl + "/asresult/resultById", null, {
        params: {
          asresultId: Number(asresultId),
        },
      }
    )
    .then((res) => {
      if (res.data.code === 1) {
        this.result = res.data.data;
      } else {
        this.$message.error(res.data.msg || "Failed to load result.");
      }
    })
    .catch(() => {
      this.$message.error("Network error.");
    });

    console.log("请求结果，asresultId =", asresultId);
  },
};
</script>

<style scoped>
.result-page {
  padding: 20px;
  max-width: 800px; /* ✅ 添加最大宽度 */
  margin: 0 auto;   /* ✅ 页面居中 */
}
.summary-card {
  margin-bottom: 20px;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}
.summary-item {
  min-width: 180px;
}
.question-card {
  margin-bottom: 18px;
}
.question-header {
  display: flex;
  align-items: center;
  gap: 10px;
}
.q-key {
  font-weight: bold;
  color: #409eff;
}
.q-tag {
  margin-left: auto;
}
.options ul {
  margin: 5px 0 0 0;
  padding-left: 20px;
}
</style>
