<template>
  <div>
    <!-- ✅ 新增：顶部操作栏 -->
    <div style="display: flex; justify-content: space-between; align-items: flex-start">
      <!-- ✅ 顶部左侧：筛选搜索 -->
      <div style="flex: 1">
        <!-- 第一行：name、role、site、status -->
        <el-form inline style="margin: 5px 0px 0px 10px">
          <el-form-item label="Name" v-if="isAdmin">
            <el-input v-model="searchForm.name" placeholder="Enter name" clearable />
          </el-form-item>
          <el-form-item label="Role" v-if="isAdmin">
            <el-select v-model="searchForm.roleId" placeholder="Select role" clearable>
              <el-option label="SuperAdmin" :value="0" />
              <el-option label="Manager" :value="1" />
              <el-option label="Employee" :value="2" />
              <el-option label="Visitor" :value="3" />
              <el-option label="Contractor" :value="4" />
            </el-select>
          </el-form-item>
          <el-form-item label="Site" v-if="isAdmin">
            <el-select v-model="searchForm.site" placeholder="Select site" clearable>
              <el-option label="Kembla" :value="1" />
              <el-option label="Padstow" :value="2" />
            </el-select>
          </el-form-item>

          <el-form-item label="Paper" v-if="isAdmin" >
            <el-select v-model="searchForm.paperId" placeholder="Select paper" clearable>
              <el-option v-for="item in paperList" :key="item.id" :label="item.title" :value="item.id" />
            </el-select>
          </el-form-item>
        </el-form>

        <!-- 第二行：paper、date + 搜索按钮 -->
        <div style="display: flex; justify-content: space-between; align-items: center; margin-left: 10px; margin-bottom: -10px;">
          <!-- 左侧字段 -->
          <el-form inline>
            <el-form-item label="Status">
              <el-select v-model="searchForm.status" placeholder="Select status" clearable>
                <el-option label="Completed" :value="0" />
                <el-option label="In progress" :value="1" />
                <el-option label="Not started" :value="2" />
                <el-option label="Retry required" :value="4" />
                <el-option label="Exam expired" :value="5" />
              </el-select>
            </el-form-item>

            <el-form-item label="Date">
              <el-date-picker
                v-model="searchForm.dateRange"
                type="daterange"
                range-separator="to"
                start-placeholder="Start date"
                end-placeholder="End date"
                format="yyyy-MM-dd"
                value-format="yyyy-MM-dd"
                clearable
              />
            </el-form-item>
          </el-form>

          <!-- 右侧按钮 -->
          <div style="margin-bottom: 22px; margin-right: 260px">
            <el-button type="primary" plain @click="fetchPaperResultList">Search</el-button>
            <el-button plain @click="handleClear">Clear</el-button>
          </div>
        </div>
      </div>

      <!-- ✅ 右侧 PDF preview -->
      <div style="margin-right: 15px; margin-top: 10px">
        <el-button type="primary" plain @click="showPrintDialog = true" size="mini" icon="el-icon-s-order">PDF preview</el-button>
      </div>
    </div>

    <!-- list清单组件 -->
    <el-table
      :data="paperResultList"
      :header-cell-style="{
        background: '#dfe6ee',
        color: '#333',
        fontWeight: 'bold',
        textAlign: 'center',
      }"
      :cell-style="{ textAlign: 'center' }"
      border
      stripe
    >
      <el-table-column label="No." type="index" width="80"></el-table-column>
      <el-table-column label="PAPER TITLE" prop="title" width="400" />
      <el-table-column label="SUBMIT TIME" prop="submitTime" width="200" :formatter="formatDate" />

      <el-table-column label="NAME" prop="username" width="150" v-if="isAdmin" />
      <el-table-column label="STATUS" width="160">
        <template slot-scope="scope">
          <el-tag :type="getStatusTagType(scope.row.status)">
            {{ getStatusText(scope.row.status) }}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column label="SCORE" width="100">
        <template slot-scope="scope">
          <span>{{ scope.row.score !== null ? scope.row.score : "-" }}</span>
        </template>
      </el-table-column>
      <el-table-column label="PASSED" width="150">
        <template slot-scope="scope">
          <span v-if="[0, 4].includes(scope.row.status)">
            <el-tag type="success" v-if="scope.row.passed == 1"> ✅</el-tag>
            <el-tag type="danger" v-else> ❌</el-tag>
          </span>
          <span v-else>-</span>
        </template>
      </el-table-column>

      <el-table-column label="ACTION" min-width="250">
        <template slot-scope="scope">
          <el-button v-if="scope.row.status === 2" type="primary" size="mini" @click="handleFill(scope.row)"> Start </el-button>
          <el-button v-if="scope.row.status === 3" type="danger" size="mini" @click="handleFill(scope.row)"> Retry </el-button>
          <el-button v-if="scope.row.status === 0 || scope.row.status === 4" type="success" size="mini" @click="goToResult(scope.row)">
            View Rresult
          </el-button>
        </template>
      </el-table-column>
    </el-table>

    <!-- ✅ 新增分页组件 -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="pageNum"
      :page-sizes="[10, 20, 50, 100, 200]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    >
    </el-pagination>

    <!-- 选择打印字段 小弹窗 -->
    <el-dialog title="Select the fields" :visible.sync="showPrintDialog" width="300px">
      <el-checkbox-group v-model="selectedFields" style="display: flex; flex-direction: column; gap: 5px; margin-left: 25px">
        <el-checkbox label="username">Name</el-checkbox>
        <el-checkbox label="identityId">Identity ID</el-checkbox>
        <el-checkbox label="email">Email</el-checkbox>
        <el-checkbox label="cname">Company</el-checkbox>
        <el-checkbox label="site">Site</el-checkbox>
        <el-checkbox label="status">Status</el-checkbox>
        <el-checkbox label="paperId">Paper ID</el-checkbox>
        <el-checkbox label="title">Paper Title</el-checkbox>
        <el-checkbox label="score">Score</el-checkbox>
        <el-checkbox label="passed">Passed</el-checkbox>
        <el-checkbox label="submitTime">SubmitTime</el-checkbox>
      </el-checkbox-group>
      <span slot="footer">
        <el-button @click="showPrintDialog = false">Cancel</el-button>
        <el-button type="primary" @click="generatePDF">Confirm</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "PaperListPage",
  data() {
    return {
      paperResultList: [],
      isAdmin: false,
      pageNum: 1,
      pageSize: 10,
      total: 0,

      showPrintDialog: false, // ✅ 打开打印字段弹窗
      selectedFields: ["username", "identityId", "cname", "email", "submitTime"], // ✅ PDF 默认选中项
      searchForm: {
        paperId: null,
        dateRange: [], // 绑定 [beginDate, endDate]
        name: null,
        roleId: null,
        site: null,
        status: null,
      },
      fieldLabels: {
        // ✅ 字段与标题映射
        username: "Name",
        identityId: "Identity ID",
        email: "Email",
        cname: "Company",
        site: "Site",
        paperId: "Paper ID",
        title: "Paper Title",
        score: "Score",
        passed: "Passed",
        submitTime: "SubmitTime",
      },
    };
  },
  created() {
    const roleId = parseInt(localStorage.getItem("roleId"));
    this.isAdmin = roleId === 0;
    this.fetchPaperResultList();
  },

  computed: {
    paperList() {
      const seen = new Set();
      return this.paperResultList
        .filter((item) => {
          if (!item.paperId || seen.has(item.paperId)) return false;
          seen.add(item.paperId);
          return true;
        })
        .map((item) => ({
          id: item.paperId,
          title: item.title,
        }));
    },
  },

  methods: {
    // ✅ 动态分页调用，加载页面
    fetchPaperResultList() {
      this.paperResultList = []; // 清空旧数据
      const userId = parseInt(localStorage.getItem("userId"));
      const userRoleId = parseInt(localStorage.getItem("roleId"));
      const [beginDate, endDate] = this.searchForm.dateRange || [];

      this.$axios
        .post(this.$httpUrl + "/asresult/fuzzyPage", {
          userId: userId,
          userRoleId: userRoleId, // ✅ 当前登录用户身份（权限控制）
          filterRoleId: this.searchForm.roleId, // ✅ 用于筛选的 role
          name: this.searchForm.name, // ✅ 新增字段
          site: this.searchForm.site, // ✅ 新增字段
          status: this.searchForm.status, // ✅ 新增字段
          paperId: this.searchForm.paperId,
          beginDate,
          endDate,
          pageNum: this.pageNum,
          pageSize: this.pageSize,
        })
        .then((res) => {
          if (res.data.code === 1) {
            this.paperResultList = res.data.data.list;
            this.total = res.data.data.total;
          }
          console.log("列表数据:", res.data.data.list);
        });
    },

    // 清除按钮
    handleClear() {
      this.searchForm = {
        paperId: null,
        dateRange: [],
      };
      this.pageNum = 1;
      this.fetchPaperResultList();
    },

    // 分页修改页码大小和跳转新页码
    handleCurrentChange(newPage) {
      this.pageNum = newPage;
      this.fetchPaperResultList();
    },
    handleSizeChange(newSize) {
      this.pageSize = newSize;
      this.pageNum = 1; // ✅ 通常重置为第一页
      this.fetchPaperResultList();
    },

    // ✅ 新增：分页切换
    handlePageChange(newPage) {
      this.pageNum = newPage;
      this.fetchPaperResultList();
    },

    // 生成PDF的confirm按钮功能，获取fuzzyNoPage的数据，获取选择好的字段
    generatePDF() {
      console.log("当前选中的字段:", this.selectedFields);
      const userId = parseInt(localStorage.getItem("userId"));
      const userRoleId = parseInt(localStorage.getItem("roleId"));
      const [beginDate, endDate] = this.searchForm.dateRange || [];
      this.$axios
        .post(
          this.$httpUrl + "/asresult/exportPdf",
          {
            filterPage: {
              ...this.searchForm,
              userId,
              userRoleId,
              beginDate,
              endDate,
            },
            selectedFields: this.selectedFields,
          },
          { responseType: "blob" }
        )
        .then((res) => {
          const blob = new Blob([res.data], { type: "application/pdf" });
          const url = window.URL.createObjectURL(blob);
          window.open(url, "_blank"); // ✅ 预览模式
          this.showPrintDialog = false;
        })
        .catch(() => {
          this.$message.error("Failed to generate PDF.");
        });
    },

    formatDate(row, column, cellValue) {
      if (!cellValue) return "-";
      const date = new Date(cellValue);
      return date.toLocaleString("en-AU", {
        timeZone: "Australia/Sydney",
        day: "2-digit",
        month: "2-digit",
        year: "numeric",
        hour: "2-digit",
        minute: "2-digit",
        hour12: true,
      });
    },

    getStatusText(status) {
      switch (status) {
        case 0:
          return "COMPLETED";
        case 1:
          return "IN PROGRESS";
        case 2:
          return "NOT STARTED";
        case 3:
          return "RETRY REQUIRED";
        case 4:
          return "FAILED";
        case 5:
          return "EXAM EXPIRED";
        default:
          return "未知";
      }
    },

    getStatusTagType(status) {
      switch (status) {
        case 0:
          return "success";
        case 1:
          return "warning";
        case 2:
          return "info";
        case 4:
          return "warning";
        case 5:
          return "danger";
        default:
          return "default";
      }
    },

    // 跳转答题页面
  handleFill(record) {
    const asresultId = record.asresultId;
    if (!asresultId) {
      this.$message.error("缺少答卷ID，无法跳转");
      return;
    }
    this.$router.push({ path: `/index/paper/fill/${asresultId}` });
  },

    goToResult(row) {
      const { asresultId } = row;
      this.$router.push(`/index/paper/result/${asresultId}`);
    },
  },
};
</script>

<style scoped>
.filter-field {
  width: 120px;
  font-size: 13px;
}
</style>
