<template>
    <el-container style="height: 100%; border: 1px solid #eee">
        <el-aside :width="asideWidth"  style=" height: 100%">
            <AsideBar :isCollapse="isCollapse"  />
        </el-aside>

        <el-container style="height: 100%">
            <el-header style="text-align: right; font-size: 12px; border-bottom: 1px solid #ccc;">
                <HeaderBar @doCollapseAside="collapseAside" :icon="iconC" />
            </el-header>

            <el-main style="height: 100%">
                <router-view />
            </el-main>
        </el-container>
    </el-container>
</template>

<script>
import AsideBar from "./Aside.vue"; // 引入子组件
import HeaderBar from "./Header.vue"; // 引入子组件

export default {
    name: "LayoutPage", // 组件名称
    // 引入子组件
    components: { AsideBar, HeaderBar}, // 注册子组件
    data() {
        return {
            // 数据
            isCollapse: false, // 是否折叠
            asideWidth: "200px", // 侧边栏宽度
            iconC: "el-icon-s-fold", // 图标
        };
    },
    methods: {
        // 方法
        collapseAside(){
            console.log("collapseAside");
            this.isCollapse = !this.isCollapse; // 切换折叠状态
            if (this.isCollapse) {
                this.asideWidth = "65px"; // 折叠后宽度
                this.iconC = "el-icon-s-unfold"; // 折叠后图标
            } else {
                this.asideWidth = "200px"; // 展开后宽度
                this.iconC = "el-icon-s-fold"; // 展开后图标
            }
        }
    },
};
</script>

<style scoped>
.el-header {
    background-color: #f3f5f7;
    color: #333;
    line-height: 60px;
}

.el-aside {
  background-color: #545c64; /* 深灰 Element UI 默认导航色 */
  height: 100%;
  padding: 0;
  box-sizing: border-box;
  margin-left: -1px;
}

.el-main {
    padding: 5px;
    height: 100%;
}
</style>
