<template>
  <div class="home-page">
    <el-card class="user-info-card">
      <h2 style="text-align: center; font-weight: bold;">Welcome, {{ username }}！</h2>

      <el-descriptions title="User Profile" :column="2" border style="margin-top: 20px;">
        <el-descriptions-item label="Account">
          {{ account }}
        </el-descriptions-item>
        <el-descriptions-item label="Role">
          <el-tag type="success">{{ roleMap[roleId] || "unknown" }}</el-tag>
        </el-descriptions-item>
        <el-descriptions-item label="Identity ID">
          {{ identityId }}
        </el-descriptions-item>
      </el-descriptions>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "HomePage",
  data() {
    return {
      username: localStorage.getItem("username") || "unknown",
      account: localStorage.getItem("account") || "unknown",
      identityId: localStorage.getItem("identityId") || "unknown",
      roleId: localStorage.getItem("roleId") || "-1",
      roleMap: {
        "0": "SuperManager",
        "1": "Manager",
        "2": "Employee",
        "3": "Visitor",
        "4": "Contractor"
      }
    };
  }
};
</script>

<style scoped>
.home-page {
  padding: 30px;
}
.user-info-card {
  max-width: 600px;
  margin: 0 auto;
}
</style>
