<template>
  <div style="height: 100%; width: 100%">
    <!-- 搜索栏 -->
    <el-form inline style="margin: 5px 0px -12px 10px">
      <el-form-item label="Account">
        <el-input v-model="accountFuzzy" placeholder="Enter account" @keyup.enter.native="loadPost" clearable />
      </el-form-item>
      <el-form-item label="Name">
        <el-input v-model="nameFuzzy" placeholder="Enter name" @keyup.enter.native="loadPost" clearable />
      </el-form-item>
      <el-form-item label="Identity ID">
        <el-input v-model="identityIdFuzzy" placeholder="Enter identity ID" clearable></el-input>
      </el-form-item>
      <!-- 搜索栏的3个按钮 -->
      <el-form-item>
        <el-button type="primary" plain @click="loadPost">Search</el-button>
        <el-button plain @click="handleClear">Clear</el-button>
        <el-button type="success" plain @click="dialogVisible = true">Create</el-button>
      </el-form-item>

      <!-- create弹窗 -->
      <el-dialog title="User Information" :visible.sync="dialogVisible" width="36%" :before-close="handleClose">
        <el-form
          :model="newUser"
          label-width="110px"
          label-position="left"
          style="margin-left: 30px"
          :rules="rules"
          ref="userForm"
          class="user-form"
        >
          <el-form-item label="Account" prop="account">
            <el-input v-model="newUser.account" />
          </el-form-item>
          <el-form-item label="Name" prop="name">
            <el-input v-model="newUser.name" />
          </el-form-item>
          <el-form-item label="Password" prop="password">
            <el-input v-model="newUser.password" />
          </el-form-item>
          <!-- <el-form-item label="Employee ID" prop="employeeId">
            <el-input v-model="newUser.employeeId" />
          </el-form-item> -->
          <el-form-item label="Email" prop="email">
            <el-input v-model="newUser.email" />
          </el-form-item>
          <el-form-item label="Site Location" prop="site">
            <el-select v-model="newUser.site" placeholder="Select location">
              <el-option label="Kembla" :value="1" />
              <el-option label="Padstow" :value="2" />
              <el-option label="Both" :value="3" />
            </el-select>
          </el-form-item>

          <el-form-item label="Company" prop="cname">
            <el-input v-model="newUser.cname" />
          </el-form-item>
          <el-form-item label="Gender">
            <el-radio-group v-model="newUser.sex" style="display: flex; flex-direction: column">
              <el-radio label="1">Male</el-radio>
              <el-radio label="0">Female</el-radio>
              <el-radio label="2">Secret</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="Phone">
            <el-input v-model="newUser.phone" />
          </el-form-item>

          <el-form-item label="Role">
            <el-select v-model="newUser.roleId" placeholder="Select role">
              <el-option label="Contractor" value="4" />
              <el-option label="Visitor" value="3" />
              <el-option label="Employee" value="2" />
              <el-option label="Manager" value="1" />
              <el-option label="Admin" value="0" />
            </el-select>
          </el-form-item>
          <el-form-item label="Main Contact">
            <el-input v-model="newUser.contact" />
          </el-form-item>
          <el-form-item label="Contact Email">
            <el-input v-model="newUser.conEmail" />
          </el-form-item>
        </el-form>

        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">Cancel</el-button>
          <el-button type="primary" @click="submitForm">Confirm</el-button>
        </span>
      </el-dialog>
    </el-form>

    <!-- 分配试卷的弹窗 -->
    <el-dialog title="Assign Paper to User" :visible.sync="assignDialogVisible" width="30%">
      <el-form label-width="100px">
        <el-form-item label="Select Paper">
          <el-select v-model="selectedPaperId" placeholder="Choose a paper" style="width: 100%; max-width: 360px">
            <el-option v-for="paper in paperList" :key="paper.paperId" :label="paper.title" :value="paper.paperId" />
          </el-select>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="assignDialogVisible = false">Cancel</el-button>
        <el-button type="primary" @click="assignPaper">Assign</el-button>
      </div>
    </el-dialog>

    <!-- userlist列表 -->
    <el-table
      style="width: 100%"
      :data="tableData"
      :header-cell-style="{
        background: '#dfe6ee',
        color: '#333',
        fontWeight: 'bold',
        textAlign: 'center',
      }"
      :cell-style="{ textAlign: 'center' }"
      border
      stripe
    >
      <el-table-column label="No." type="index" width="80"></el-table-column>
      <el-table-column prop="account" label="ACCOUNT" width="150"></el-table-column>
      <el-table-column prop="name" label="NAME" width="150"> </el-table-column>
      <el-table-column prop="identityId" label="IDENTITY_ID" width="150"></el-table-column>
      <el-table-column prop="roleId" label="ROLE" width="180">
        <template slot-scope="scope">
          <el-tag :type="roleColors[scope.row.roleId]" disable-transitions>{{ roleMap[scope.row.roleId] || "Unknown Role" }}</el-tag>
        </template>
      </el-table-column>
      <!-- ✅ 新增字段 -->
      <el-table-column prop="email" label="EMAIL" width="220" />
      <el-table-column prop="contact" label="MAIN CONTACT" width="150" />
      <el-table-column prop="conEmail" label="CONTACT EMAIL" width="220" />
      <el-table-column prop="cname" label="COMPANY NAME" width="150" />

      <el-table-column prop="site" label="SITE LOCATION" width="180">
        <template slot-scope="scope">
          {{ siteMap[scope.row.site] || "Unknown" }}
        </template>
      </el-table-column>

      <!-- ACTION操作栏 -->
      <el-table-column prop="operate" label="ACTIONS" min-width="220" v-slot="scope">
        <div style="display: flex; gap: 6px; justify-content: center">
          <!-- <el-button size="small" type="primary" plain>Edit</el-button> -->
          <el-button size="small" type="warning" plain @click="openAssignDialog(scope.row)">Assign</el-button>
          <el-button size="small" type="danger" @click="handleDelete(scope.row)">Delete</el-button>
        </div>
      </el-table-column>
    </el-table>

    <!-- 分页功能 -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="pageNum"
      :page-sizes="[10, 15, 20, 50]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    >
    </el-pagination>
  </div>
</template>

<script>
export default {
  name: "UserListPage",
  data() {
    return {
      // 分配试卷相关
      assignDialogVisible: false,
      selectedUserId: null,
      selectedPaperId: null,
      paperList: [],

      siteMap: {
        1: "Port Kembla",
        2: "Padstow",
        3: "Both",
      },

      // 弹窗
      dialogVisible: false,
      newUser: {
        account: "",
        name: "",
        employeeId: "",
        password: "",
        roleId: "3", // 默认角色为普通用户
        cname: "", // 公司名称
        sex: "2", // 默认性别为保密
        phone: "",
        email: "",
        contact: "",
        conEmail: "",
        site: null,
      },
      // 表单验证规则
      // 账号、密码、姓名、工号不能为空
      rules: {
        account: [
          {
            required: true,
            pattern: /[a-zA-Z]/,
            message: "Must contain at least one letter",
            trigger: "blur",
          },
        ],
        password: [
          {
            required: true,
            pattern: /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{6,}$/,
            message: "At least 6 chars, must mixed letters and numbers",
            trigger: "blur",
          },
        ],
        name: [{ required: true, message: "Name is required", trigger: "blur" }],
        employeeId: [
          {
            required: true,
            message: "Employee ID is required",
            trigger: "blur",
          },
        ],
        email: [
          {
            required: true,
            pattern: /^[^\s@]+@[^\s@]+\.[^\s@]+$/,
            message: "Email is required",
            trigger: "blur",
          },
        ],
        site: [{ required: true, message: "Site is required", trigger: "change" }],
        cname: [
          {
            required: true,
            message: "Company name is required",
            trigger: "blur",
          },
        ],
      },

      // 分页参数
      pageNum: 1,
      pageSize: 10,
      total: 0,
      nameFuzzy: "",
      accountFuzzy: "",
      identityIdFuzzy: "",
      // 表格数据
      tableData: [],
      roleMap: {
        0: "Super Admin",
        1: "Manager",
        2: "Employee",
        3: "Visitor",
        4: "Contractor",
      },
      roleColors: {
        0: "primary",
        1: "success",
        2: "info",
        3: "info",
        4: "info",
      },
    };
  },
  beforeMount() {
    // 组件挂载前
    this.loadPost();
  },
  methods: {
    // 方法
    // Clear按钮功能
    handleClear() {
      this.nameFuzzy = "";
      this.accountFuzzy = "";
      this.identityIdFuzzy = "";
      this.loadPost();
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.pageSize = val;
      this.pageNum = 1; // 重置页码为1
      this.loadPost();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.pageNum = val;
      this.loadPost();
    },
    // 关闭弹窗
    handleClose(done) {
      this.$confirm("Are you sure to close this dialog?")
        .then(() => {
          done();
        })
        .catch(() => {});
    },
    // 提交表单，创建用户
    submitForm() {
      this.$refs.userForm.validate((valid) => {
        if (!valid) {
          this.$message.warning("Please complete the required fields correctly");
          return;
        }

        this.$axios
          .post(this.$httpUrl + "/user/save", this.newUser)
          .then((res) => {
            if (res.data.code == 1) {
              this.$message.success("Saved successfully");

              // ✅ 设置返回的 identityId 到前端字段中
              if (res.data.data && res.data.data.identityId) {
                this.newUser.identityId = res.data.data.identityId;
              }

              this.dialogVisible = false; // 关闭弹窗
              this.loadPost(); // 刷新列表
            } else {
              this.$message.error(res.data.msg || "Save failed");
            }
          })
          .catch(() => {
            this.$message.error("Server error");
          });
      });
    },

    // 删除用户
    handleDelete(row) {
      this.$confirm(`Are you sure to delete user "${row.name}"?`, "Warning", {
        confirmButtonText: "Yes",
        cancelButtonText: "No",
        type: "warning",
      })
        .then(() => {
          // 发请求到后端
          this.$axios.delete(this.$httpUrl + "/user/delete?id=" + row.userId).then((res) => {
            if (res.data.code == 1) {
              this.$message.success("User deleted");
              this.loadPost(); // 重新加载数据
            } else {
              this.$message.error(res.data.msg || "Delete failed");
            }
          });
        })
        .catch(() => {
          this.$message.info("Delete canceled");
        });
    },

    // 打开分配试卷的弹窗
    openAssignDialog(row) {
      this.selectedUserId = row.userId;
      this.assignDialogVisible = true;
      console.log("Assigning paper to user ID:", this.selectedUserId);

      // 获取所有试卷列表供选择
      this.$axios.get(this.$httpUrl + "/paper/listAll").then((res) => {
        if (res.data.code === 1) {
          this.paperList = res.data.data;
        } else {
          this.$message.error("Failed to load papers.");
        }
      });
    },
    // 分配试卷
    assignPaper() {
      console.log("Assigning paper", this.selectedPaperId, "to user", this.selectedUserId);
      this.$axios
        .post(this.$httpUrl + "/asresult/insert", {
          userId: this.selectedUserId,
          paperId: this.selectedPaperId,
        })
        .then((res) => {
          if (res.data.code === 1) {
            this.$message.success("Paper assigned successfully!");
            this.assignDialogVisible = false;
          } else {
            this.$message.error(res.data.msg || "Assignment failed.");
          }
        });
    },

    // 发送 GET 请求，仅供参考，用分页查询
    loadGet() {
      this.$axios.get(this.$httpUrl + "/user/list").then((response) => {
        // 处理响应数据
        console.log(response.data);
      });
    },

    // 发送 POST 请求，分页筛选查询
    loadPost() {
      this.$axios
        .post(
          this.$httpUrl + "/user/listPage",
          {
            name: this.nameFuzzy, // name模糊查询
            account: this.accountFuzzy, // account模糊查询
            identityId: this.identityIdFuzzy, // identityId模糊查询
            pageBody: {
              pageNum: this.pageNum,
              pageSize: this.pageSize,
            },
          }
        )
        .then((response) => {
          // 处理响应数据
          if (response.data.code == 1) {
            console.log("分页完整数据：", response.data);
            this.tableData = response.data.data.list;
            this.total = response.data.data.total;
            this.pageNum = response.data.data.pageNum;
            this.pageSize = response.data.data.pageSize;
          } else {
            alert("Get data failed");
          }
        });
    },
  },
  computed: {
    roleId() {
      return Number(localStorage.getItem("roleId"));
    },
    isAdmin() {
      return this.roleId === 0;
    },
  },
};
</script>

<style scoped>
/* 样式 */
.user-form .el-input,
.user-form .el-select,
.user-form .el-radio-group {
  width: 280px; /* 改成你想要的宽度 */
}
</style>
